  font-weight: bold; }

/* Tables
   ========================================================================== */
/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

/**
 * CLEARFIX
 */
.clear:after {
  display: table;
  width: 100%;
  content: '';
  clear: both; }

html,
body {
  min-height: 100%;
  color: #333;
  min-width: 1100px;
  font-family: "FiraSans", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400; }

body {
  overflow-y: scroll;
  background: #f1f1f1; }
  body * {
    box-sizing: border-box; }
  body a {
    color: #f4645f;
    text-decoration: underline; }
    body a:hover {
      color: #f1362f; }
  body .content {
    max-width: 1100px;
    margin: 0 auto;
    width: 100%; }
  body h1 {
    font-size: 38px; }
  body h1, body h2, body h3 {
    font-weight: 100;
    color: #525252; }
  body h3 {
    font-weight: 300; }
